/**
 * Custom CSS
 */
 @import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600&display=swap');
 @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');

 span, p, label, div {
  font-family: 'Source Sans Pro', Arial, sans-serif;
}

button, a, a:before, a:after {
  font-family: 'Source Sans Pro', Arial, sans-serif;
  border: 0;
  outline: 0;

  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}


 h1 {
   font-family: 'Monstrat', Helvetica, sans-serif;
   font-weight: 700;
   line-height: 1.25em;
   color: #fff;
   text-transform: uppercase;
   font-size: 2.25rem;
   text-shadow: 0 0 20px rgb(255 255 255 / 60%);
   letter-spacing: 0.1em;
 }

 h2, h3, h4, h5, h6 {
   font-family: 'Monstrat', Helvetica, sans-serif;
   font-weight: 700;
   line-height: 1.25em;
 }

/*
 *  SCROLL STYLES - https://codepen.io/GhostRider/pen/GHaFw
 */
/* Secondary */
#scrollstyle-secondary::-webkit-scrollbar-track {
	background-color: #EEE;
	border-radius: 10px;
}

#scrollstyle-secondary::-webkit-scrollbar {
	width: 6px;
	background-color: #EEE;
}

#scrollstyle-secondary::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background-image: -webkit-gradient(linear,
									   left top,
									   left bottom,
									   color-stop(0.44, #35c5f3),
									   color-stop(0.72, #35b0f3),
									   color-stop(0.86, #35a1f3));
}

/* style modification for react select */
.select__control{
  height: 100% !important;
  border-radius: 0 !important;
  border: 1px solid black !important;

  box-shadow: unset !important;
}
.select__control:hover{
  border-color: black !important;
}

.basic-single{
  position: absolute !important;
  width: 20% !important;
  min-width: 200px !important;
  z-index: 1 !important;
  height: 100% !important;
  border-radius: 0 !important;
}